.btn-empty {
    background: $color-light;
    color: $color-primary;
    border: 2px solid $color-primary;
    padding: 8.5px 25px;
}
.btn-filled {
    background: $color-primary;
    color: $color-light;
    border:none;
    padding: 0px 30px;
}
.btn-default {
    border-radius: 22.5px;
    font-size: 20px;
    line-height: 45px;
    font-family: $font-family-text;
    text-decoration: none;
    margin: none;
    font-weight: $font-weight-medium;
}
